.robots__contact {
    display: flex;
    flex-direction: row;
}

.robots__contact-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.robots__contact-image img {
        width:100%;
        height: 100%;
}

.robots__contact-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.robots__contact-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}
.robots__contact-content h4:last-child {
    color: #ff8A71;
}
.robots__contact-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}
.robots__contact-content p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .robots__contact {
        flex-direction: column;
    }
    .robots__contact-image {
        margin: 1rem 0;
    }
    .robots__contact-content {
        margin-top: 2rem;
    }
}